import { fetchItems } from "./remotes";
import createIssue from './github';
import candidates from './data/candidates';

async function main() {
  const sections = [];

  await Promise.all(
    candidates.map(async candidate => {
      const items = await fetchItems(candidate);
      const section = [
        `# ${candidate.id}: ${items.length}`,
        "",
        ...items.map(
          ({
            name,
            pid,
            price,
            image,
            updateTime
          }) => {
            const title = `## [${name}]`;
            const thumbnail = `<img src=${image} />`;
            const kprice = `${price} `;
            
            return [
              title,
              thumbnail,
              "",
              ` |  |  |`,
              ` |  -  |  -  |`,
              ` | ${name} | ${kprice} |`,
            ].join("\n");
          }
        )
      ].join("\n");

      sections.push(section);
    })
);

  const body = sections.join("\n\n");
  const time = Date.now();
  createIssue(`${new Date(time).toISOString().slice(0, 10)}   `, body);
};

main();